@import "../../global.scss";

.works {
    background-color: crimson;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    overflow: hidden;

    .arrow {
        height: 50px;
        position: absolute;
        cursor: pointer;

        @include mobile {
            display: none;
        }

        &.left {
            left: 100px;
            transform: rotateY(180deg);
        }
        &.right {
            right: 100px;
        }
    }
    .slider {
        height: 350px;
        display: flex;
        position: absolute;
        left: 0;
        transition: all 1s ease-out;

        @include mobile {
            height: 100vh;
            flex-direction: column;
            justify-content: center;
        }
    }

    .container {
        width: 100vw;
        display: flex;
        align-items: center;
        justify-content: center;
        overflow: hidden;

        .item {
            width: 700px;
            height: 100%;
            background-color: white;
            border-radius: 20px;
            display: flex;
            align-items: center;
            justify-content: center;

            @include mobile {
                width: 80%;
                height: 150px;
                margin: 15px 0;
            }

            .left {
                flex: 4;
                height: 80%;
                display: flex;
                justify-content: center;
                align-items: center;

                .leftContainer {
                    width: 90%;
                    height: 70%;
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;
                    margin-left: 10px;

                    @include mobile {
                        width: 100%;
                        height: 100%;
                    }

                    .imgContainer {
                        width: 40px;
                        height: 40px;
                        border-radius: 50%;
                        background-color: rgb(245, 179, 155);
                        display: flex;
                        justify-content: center;
                        align-items: center;

                        @include mobile {
                            width: 20px;
                            height: 20px;
                        }

                        img {
                            width: 25px;

                            @include mobile {
                                width: 15px;
                            }
                        }
                    }

                    h2 {
                        font-size: 20px;

                        @include mobile {
                            font-size: 13px;
                        }
                    }
                    p {
                        font-style: 13px;

                        @include mobile {
                            display: none;
                        }
                    }

                    span {
                        font-style: 12px;
                        font-weight: 600;
                        text-decoration: underline;
                        cursor: pointer;
                    }
                }
            }
            .right {
                flex: 8;
                height: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                overflow: hidden;
                img {
                    width: 400px;
                    transform: rotate(-10deg);
                }
            }
        }
    }
}
